.contact-block {
	display: flex;
	align-items: center;
	@include cover;
	text-align: center;
	&__phone {
		margin-bottom: 10px; }
	&__email {
		font-size: 35px;
		line-height: 35px;
		color: $white;
		border-bottom: 1px solid #cfebee;
		@include tr(border-color .3s);
		@include hover {
			border-bottom-color: transparent; } }
	&__address {
		margin: 15px 0 33px;
		font-style: normal;
		font-size: 20px;
		line-height: 25px;
		color: $white;
		text-align: center; }
	&__actions {
		margin: 0 -20px;
		&-btn {
			margin:  0 10px;
			padding: 11px 39px; } }

	&__form {
		max-width: 795px;
		margin: 30px auto 0;
		text-align: center; }
	&__form-row {
		display: flex;
		& + & {
			margin-top: 20px; } }
	&__form-field {
		flex: 1 1 255px;
		margin: 0 5px; }
	&__form-descr {
		margin: 15px 0 7px;
		color: $white;
		font-size: 15px;
		line-height: 25px; }
	&__input {
		padding: 0 23px;
		@include f('pn-300', 16); }
	&__textarea {
		min-height: 100px;
		margin: 0 5px; }
	&_has-address {
		padding: 80px 0 90px; }
	&_has-address &__container {
		display: flex;
		flex-direction: column;
		align-items: center;
		flex: 1 1 100%;
		max-width: 550px; }

	&_has-form {
		padding: 94px 0 81px; } }
