/* ===================================
	 main
	 @mixin ================================== */
.main {
	&_home {
		margin-top: -340px; } }

/* ===================================
	 logo
	 @mixin ================================== */
.logo {
	display: block;
	flex: 0 0 160px;
	&_white {
		@include s('logo'); }
	&_shadow {
		flex: 0 0 192px;
		@include s('logo-shadow'); }
	&_color {
		@include s('logo-color'); } }

/* paritcles
	----------------------------------- */
.particles {
	@include coverdiv;
	z-index: 1;
	@include nosel; }

/* sliders-default
	----------------------------------- */
.slider-btn {
	position: absolute;
	top: calc(50% - 20px);
	z-index: 1;
	width: 40px;
	height: 40px;
	background: $aqua;
	transition: background .3s ease;
	@include vertical;
	@include hover {
		background: $aqua-dark; }
	&_prev {
		.icon-arr {
			transform: scaleX(-1) translate3d(0, 0, 0); } }
	//&_next
	&__icon {
		display: inline-block;
		vertical-align: middle;
		fill: $white;
		font-size: 12px; } }

